<template>
  <view>
    <view class="uni-header">
      <view class="uni-group">
        <view class="uni-title"></view>
        <view class="uni-sub-title"></view>
      </view>
      <view class="uni-group">
        <input class="uni-search" type="text" v-model="query" @confirm="search" placeholder="请输入搜索内容" />
        <button class="uni-button" type="default" size="mini" @click="search">搜索</button>
        <button class="uni-button" type="default" size="mini" @click="navigateTo('./add')">新增</button>
        <button class="uni-button" type="default" size="mini" :disabled="!selectedIndexs.length" @click="delTable">批量删除</button>
        <download-excel class="hide-on-phone" :fields="exportExcel.fields" :data="exportExcelData" :type="exportExcel.type" :name="exportExcel.filename">
          <button class="uni-button" type="primary" size="mini">导出 Excel</button>
        </download-excel>
      </view>
    </view>
    <view class="uni-container">
      <unicloud-db ref="udb" :collection="collectionList" :where="where" page-data="replace"
        :orderby="orderby" :getcount="true" :page-size="options.pageSize" :page-current="options.pageCurrent"
        v-slot:default="{data,pagination,loading,error,options}" :options="options" loadtime="manual" @load="onqueryload">
        <uni-table ref="table" :loading="loading" :emptyText="error.message || '没有更多数据'" border stripe type="selection" @selection-change="selectionChange">
          <uni-tr>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'avatarUrl')">头像</uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'username')" sortable @sort-change="sortChange($event, 'username')">姓名</uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'phone')" sortable @sort-change="sortChange($event, 'phone')">手机号</uni-th>
            <uni-th align="center" filter-type="select" :filter-data="options.filterData.gender_localdata" @filter-change="filterChange($event, 'gender')">性别</uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'age')" sortable @sort-change="sortChange($event, 'age')">年龄</uni-th>
            <uni-th align="center" filter-type="range" @filter-change="filterChange($event, 'balance')" sortable @sort-change="sortChange($event, 'balance')">余额</uni-th>
            <uni-th align="center">所属商家</uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'contactAddress')" sortable @sort-change="sortChange($event, 'contactAddress')">联系地址</uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'residentAddress')" sortable @sort-change="sortChange($event, 'residentAddress')">常驻地址</uni-th>
            <uni-th align="center" filter-type="range" @filter-change="filterChange($event, 'latitude')" sortable @sort-change="sortChange($event, 'latitude')">纬度</uni-th>
            <uni-th align="center" filter-type="range" @filter-change="filterChange($event, 'longitude')" sortable @sort-change="sortChange($event, 'longitude')">经度</uni-th>
            <uni-th align="center" filter-type="range" @filter-change="filterChange($event, 'level ')" sortable @sort-change="sortChange($event, 'level ')">等级</uni-th>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'workPhoto')">工作照</uni-th>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'lifePhoto')">生活照</uni-th>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'IDFront')">身份证正面</uni-th>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'IDReverse')">身份证反面</uni-th>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'skillCertificate')">技能证书</uni-th>
            <uni-th align="center">所属分类</uni-th>
            <uni-th align="center" filter-type="select" :filter-data="options.filterData.auditStatus_localdata" @filter-change="filterChange($event, 'auditStatus')">审核状态</uni-th>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'isEnable')">生效状态</uni-th>
            <uni-th align="center">操作</uni-th>
          </uni-tr>
          <uni-tr v-for="(item,index) in data" :key="index">
            <uni-td align="center">
              <uni-file-picker v-if="item.avatarUrl && item.avatarUrl.fileType == 'image'" :value="item.avatarUrl" :file-mediatype="item.avatarUrl && item.avatarUrl.fileType" return-type="object" :imageStyles="imageStyles" readonly></uni-file-picker>
              <uni-link v-else :href="item.avatarUrl && item.avatarUrl.url" :text="item.avatarUrl && item.avatarUrl.url"></uni-link>
            </uni-td>
            <uni-td align="center">{{item.username}}</uni-td>
            <uni-td align="center">{{item.phone}}</uni-td>
            <uni-td align="center">{{options.gender_valuetotext[item.gender]}}</uni-td>
            <uni-td align="center">{{item.age}}</uni-td>
            <uni-td align="center">{{item.balance}}</uni-td>
            <uni-td align="center">{{item.merchantID && item.merchantID[0] && item.merchantID[0].text}}</uni-td>
            <uni-td align="center">{{item.contactAddress}}</uni-td>
            <uni-td align="center">{{item.residentAddress}}</uni-td>
            <uni-td align="center">{{item.latitude}}</uni-td>
            <uni-td align="center">{{item.longitude}}</uni-td>
            <uni-td align="center">{{item.level }}</uni-td>
            <uni-td align="center">
              <uni-file-picker v-if="item.workPhoto && item.workPhoto.fileType == 'image'" :value="item.workPhoto" :file-mediatype="item.workPhoto && item.workPhoto.fileType" return-type="object" :imageStyles="imageStyles" readonly></uni-file-picker>
              <uni-link v-else :href="item.workPhoto && item.workPhoto.url" :text="item.workPhoto && item.workPhoto.url"></uni-link>
            </uni-td>
            <uni-td align="center">
              <uni-file-picker v-if="item.lifePhoto && item.lifePhoto.fileType == 'image'" :value="item.lifePhoto" :file-mediatype="item.lifePhoto && item.lifePhoto.fileType" return-type="object" :imageStyles="imageStyles" readonly></uni-file-picker>
              <uni-link v-else :href="item.lifePhoto && item.lifePhoto.url" :text="item.lifePhoto && item.lifePhoto.url"></uni-link>
            </uni-td>
            <uni-td align="center">
              <uni-file-picker v-if="item.IDFront && item.IDFront.fileType == 'image'" :value="item.IDFront" :file-mediatype="item.IDFront && item.IDFront.fileType" return-type="object" :imageStyles="imageStyles" readonly></uni-file-picker>
              <uni-link v-else :href="item.IDFront && item.IDFront.url" :text="item.IDFront && item.IDFront.url"></uni-link>
            </uni-td>
            <uni-td align="center">
              <uni-file-picker v-if="item.IDReverse && item.IDReverse.fileType == 'image'" :value="item.IDReverse" :file-mediatype="item.IDReverse && item.IDReverse.fileType" return-type="object" :imageStyles="imageStyles" readonly></uni-file-picker>
              <uni-link v-else :href="item.IDReverse && item.IDReverse.url" :text="item.IDReverse && item.IDReverse.url"></uni-link>
            </uni-td>
            <uni-td align="center">
              <template v-for="(file, j) in item.skillCertificate">
                <uni-file-picker v-if="file.fileType == 'image'" :value="file" :file-mediatype="file.fileType" :imageStyles="imageStyles" readonly></uni-file-picker>
                <uni-link v-else :href="file.url" :text="file.url"></uni-link>
              </template>
            </uni-td>
            <uni-td align="center">{{item.classType}}</uni-td>
            <uni-td align="center">{{options.auditStatus_valuetotext[item.auditStatus]}}</uni-td>
            <uni-td align="center">{{item.isEnable == true ? '✅' : '❌'}}</uni-td>
            <uni-td align="center">
              <view class="uni-group">
                <button @click="navigateTo('./edit?id='+item._id, false)" class="uni-button" size="mini" type="primary">修改</button>
                <button @click="confirmDelete(item._id)" class="uni-button" size="mini" type="warn">删除</button>
              </view>
            </uni-td>
          </uni-tr>
        </uni-table>
        <view class="uni-pagination-box">
          <uni-pagination show-icon :page-size="pagination.size" v-model="pagination.current" :total="pagination.count" @change="onPageChanged" />
        </view>
      </unicloud-db>
    </view>
  </view>
</template>

<script>
  import { enumConverter, filterToWhere } from '../../js_sdk/validator/worker.js';

  const db = uniCloud.database()
  // 表查询配置
  const dbOrderBy = '' // 排序字段
  const dbSearchFields = [] // 模糊搜索字段，支持模糊搜索的字段列表。联表查询格式: 主表字段名.副表字段名，例如用户表关联角色表 role.role_name
  // 分页配置
  const pageSize = 20
  const pageCurrent = 1

  const orderByMapping = {
    "ascending": "asc",
    "descending": "desc"
  }

  export default {
    data() {
      return {
        collectionList: [ db.collection('worker').field('avatarUrl,username,phone,gender,age,balance,merchantID,contactAddress,residentAddress,latitude,longitude,level ,workPhoto,lifePhoto,IDFront,IDReverse,skillCertificate,classType,auditStatus,isEnable').getTemp(),db.collection('merchant').field('merchantName as text,_id').getTemp(),db.collection('classType').field('classTypeName as text,_id').getTemp() ],
        query: '',
        where: '',
        orderby: dbOrderBy,
        orderByFieldName: "",
        selectedIndexs: [],
        options: {
          pageSize,
          pageCurrent,
          filterData: {
            "gender_localdata": [
              {
                "text": "男",
                "value": 1
              },
              {
                "text": "女",
                "value": 2
              }
            ],
            "auditStatus_localdata": [
              {
                "text": "审核中",
                "value": 1
              },
              {
                "text": "通过",
                "value": 2
              },
              {
                "text": "不通过",
                "value": 3
              }
            ]
          },
          ...enumConverter
        },
        imageStyles: {
          width: 64,
          height: 64
        },
        exportExcel: {
          "filename": "worker.xls",
          "type": "xls",
          "fields": {
            "头像": "avatarUrl",
            "姓名": "username",
            "手机号": "phone",
            "性别": "gender",
            "年龄": "age",
            "余额": "balance",
            "所属商家": "merchantID",
            "联系地址": "contactAddress",
            "常驻地址": "residentAddress",
            "纬度": "latitude",
            "经度": "longitude",
            "等级": "level ",
            "工作照": "workPhoto",
            "生活照": "lifePhoto",
            "身份证正面": "IDFront",
            "身份证反面": "IDReverse",
            "技能证书": "skillCertificate",
            "所属分类": "classType",
            "审核状态": "auditStatus",
            "生效状态": "isEnable"
          }
        },
        exportExcelData: []
      }
    },
    onLoad() {
      this._filter = {}
    },
    onReady() {
      this.$refs.udb.loadData()
    },
    methods: {
      onqueryload(data) {
        this.exportExcelData = data
      },
      getWhere() {
        const query = this.query.trim()
        if (!query) {
          return ''
        }
        const queryRe = new RegExp(query, 'i')
        return dbSearchFields.map(name => queryRe + '.test(' + name + ')').join(' || ')
      },
      search() {
        const newWhere = this.getWhere()
        this.where = newWhere
        this.$nextTick(() => {
          this.loadData()
        })
      },
      loadData(clear = true) {
        this.$refs.udb.loadData({
          clear
        })
      },
      onPageChanged(e) {
        this.selectedIndexs.length = 0
        this.$refs.table.clearSelection()
        this.$refs.udb.loadData({
          current: e.current
        })
      },
      navigateTo(url, clear) {
        // clear 表示刷新列表时是否清除页码，true 表示刷新并回到列表第 1 页，默认为 true
        uni.navigateTo({
          url,
          events: {
            refreshData: () => {
              this.loadData(clear)
            }
          }
        })
      },
      // 多选处理
      selectedItems() {
        var dataList = this.$refs.udb.dataList
        return this.selectedIndexs.map(i => dataList[i]._id)
      },
      // 批量删除
      delTable() {
        this.$refs.udb.remove(this.selectedItems(), {
          success:(res) => {
            this.$refs.table.clearSelection()
          }
        })
      },
      // 多选
      selectionChange(e) {
        this.selectedIndexs = e.detail.index
      },
      confirmDelete(id) {
        this.$refs.udb.remove(id, {
          success:(res) => {
            this.$refs.table.clearSelection()
          }
        })
      },
      sortChange(e, name) {
        this.orderByFieldName = name;
        if (e.order) {
          this.orderby = name + ' ' + orderByMapping[e.order]
        } else {
          this.orderby = ''
        }
        this.$refs.table.clearSelection()
        this.$nextTick(() => {
          this.$refs.udb.loadData()
        })
      },
      filterChange(e, name) {
        this._filter[name] = {
          type: e.filterType,
          value: e.filter
        }
        let newWhere = filterToWhere(this._filter, db.command)
        if (Object.keys(newWhere).length) {
          this.where = newWhere
        } else {
          this.where = ''
        }
        this.$nextTick(() => {
          this.$refs.udb.loadData()
        })
      }
    }
  }
</script>

<style>
</style>
